import React, { Component } from 'react'
import { BrowserRouter, Switch, Route } from 'react-router-dom'
import "./css/main.css";
import ConstructionPage from './pages/ConstructionPage';

class App extends Component {
    render() {
        return (
            <BrowserRouter>
                <Switch>
                    <Route path="/test">
                        <h1>Test</h1>
                    </Route>
                    <Route path="/"><ConstructionPage /></Route>
                </Switch>
            </BrowserRouter>
        )
    }
}

export default App;
